.textcontainer{    
  background: url(/antonydev/static/media/picture2.cf71df4a.jpg);
  background-size: cover;
  background-position: center center;
  color: #fff;
    font-weight:300;
    font-size:32px;
    -webkit-backface-visibility: hidden;
    -webkit-perspective: 1000;
    -webkit-transform: translate3d(0,0,0);
    height: 180px;
    display: flex;
    justify-content: center;
    align-items: center;  
    background-attachment: fixed;
}

.textanimation {
  display:inline-block;
  overflow:hidden;
  white-space:nowrap;
  margin: 0 2px;
}
.textanimation:first-of-type {    /* For increasing performance 
                       ID/Class should've been used. 
                       For a small demo 
                       it's okaish for now */
  animation: showup 6s infinite;
}

.textanimation:last-of-type {
  width:0px;
  animation: reveal 6s infinite;
}

.textanimation:last-of-type span {
  margin-left:-355px;
  animation: slidein 6s infinite;
}

@keyframes showup {
    0% {opacity:0;}
    20% {opacity:1;}
    80% {opacity:1;}
    100% {opacity:0;}
}

@keyframes slidein {
    0% { margin-left:-800px; }
    20% { margin-left:-800px; }
    35% { margin-left:0px; }
    100% { margin-left:0px; }
}

@keyframes reveal {
    0% {opacity:0;width:0px;}
    20% {opacity:1;width:0px;}
    30% {width:355px;}
    80% {opacity:1;}
    100% {opacity:0;width:355px;}
}

@media screen and (max-width: 960px) {
  .textcontainer{
    font-size: 27px;
    height: 100px;
  }
  
}
